html {
  text-transform: uppercase;
  --active-color: #00acdc;
  --default-color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  box-sizing: border-box;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-align: center;
  color: var(--default-color);
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
